import { HttpService } from './../../services/http.service';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Vote } from 'src/app/models/vote';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-contestant-details',
  templateUrl: './contestant-details.component.html',
  styleUrls: ['./contestant-details.component.css']
})
export class ContestantDetailsComponent implements OnInit {
  private id: string;
  private vote: Vote;
  private contestant: any;
  private contents: any[];
  private canVote: boolean;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private title: Title,
    private http: HttpService
  ) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.id = params.id;

      this.loadContestant();
    });
  }

  loadContestant() {
    this.http
      .request(`contestant/${this.id}`, { method: 'GET' })
      .then(d => {
        this.contestant = d;

        this.loadVote();
        this.loadImages();
      });
  }

  loadVote() {
    this.http
      .request(`vote/${this.contestant.VoteId}`, { method: 'GET' })
      .then((vote: Vote) => {
        this.vote = vote;
        this.title.setTitle(vote.Name);

        const timeStarting = new Date(this.vote.VoteStartingTime).getTime();
        const timeClosing = new Date(this.vote.VoteClosingTime).getTime();
        const timeNow = new Date().getTime();

        if (timeNow < timeStarting) { this.canVote = true; }
      });
  }

  loadImages() {
    this.http
      .request(`contestantContent/${this.id}`, { method: 'GET' })
      .then(contents => {
        this.contents = contents;
      });
  }

  onVotingClick = () => {

  }
}
