import { Vote } from './../../models/vote';
import { Component, OnInit } from '@angular/core';
import { en_US, ru_RU, zh_CN, sv_SE, da_DK } from 'ng-zorro-antd-mobile';
import { Toast } from 'ng-zorro-antd-mobile';
import { HttpService } from './../../services/http.service';

@Component({
  selector: 'app-create-vote',
  templateUrl: './create-vote.component.html',
  styleUrls: ['./create-vote.component.css'],
  providers: [Toast]
})
export class CreateVoteComponent implements OnInit {
  locale = zh_CN;
  beginTime = new Date();
  endTime = new Date();

  model: Vote;

  constructor(private http: HttpService, private toast: Toast) {
    this.model = new Vote();
    this.model.Id = '';
    this.model.EnrollDeadlines = new Date();
    this.model.Name = '';
    this.model.VoteStartingTime = new Date();
    this.model.VoteClosingTime = new Date();
    this.model.Rules = '';
    this.model.Visits = 0;
  }

  ngOnInit() {
  }

  showToast(msg: string) {
    Toast.info(msg, 3000, null, false);
  }

  onSave() {
    if (this.model.Name === '') { this.showToast('请输入投票主题'); return; }
    if (this.model.VoteStartingTime.getTime() >= this.model.VoteClosingTime.getTime()) { this.showToast('开始时间必须小于结束时间'); return; }
    if (this.model.Rules === '') { this.showToast('请录入投票说明'); return; }

    this.http
      .request('vote', { method: 'POST', body: this.model })
      .then(id => { this.showToast('投票发起成功'); });
  }

  currentDateFormat(date, format: string = 'yyyy-mm-dd HH:MM'): any {
    const pad = (n: number): string => (n < 10 ? `0${n}` : n.toString());
    return format
      .replace('yyyy', date.getFullYear())
      .replace('mm', pad(date.getMonth() + 1))
      .replace('dd', pad(date.getDate()));
    // .replace('HH', pad(date.getHours()))
    // .replace('MM', pad(date.getMinutes()))
    // .replace('ss', pad(date.getSeconds()));
  }

  onBeginTimeOk(result: Date) {
    this.beginTime = result;
  }

  onEndTimeOk(result: Date) {
    this.endTime = result;
  }
}
