import { JoinComponent } from './components/join/join.component';
import { AppComponent } from './app.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './components/home/home.component';
import { CreateVoteComponent } from './components/create-vote/create-vote.component';
import { VoteDetailsComponent } from './components/vote-details/vote-details.component';
import { ContestantDetailsComponent } from './components/contestant-details/contestant-details.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'home', component: HomeComponent },
  { path: 'new', component: CreateVoteComponent },
  { path: 'v/:id', component: VoteDetailsComponent },
  { path: 'join/:id', component: JoinComponent },
  { path: 'c/:id', component: ContestantDetailsComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
