import { Injectable, Inject } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class HttpService {

  constructor() { }

  public async requestAsync(url: string, options: any) {
    let result: any = null;
    const response = await this.fetch(url, options);

    await this.checkStatus(response);

    const promise: any = response.json();

    await promise.then((data) => {
      if (data.success) {
        result = data.result;
      } else {
        throw new Error(data.result.message);
      }
    });

    return result;
  }

  public requestWithText(url: string, options: any) {
    url = `http://localhost:9999/apis/${url}`;

    return this.fetch(url, options)
      .then(this.checkStatus)
      .then(response => {
        const data = response.text();
        return data;
      })
      .then(data => {
        return data;
      });
  }

  public request(url: string, options: any) {
    url = `http://localhost:9999/apis/${url}`;

    return this.fetch(url, options)
      .then(this.checkStatus)
      .then(response => {
        const data = response.json();
        return data;
      })
      .then(data => {
        return data;
      });
  }

  private fetch(url: string, options: any) {
    options = this.setHeader(options);

    return fetch(url, options);
  }

  private setHeader(options: any) {
    const defaultOptions = {
      credentials: 'include', model: 'cors'
    };

    const tokenDodu = 'dodu-'; // this.cookie.get('token');

    const newOptions = { ...defaultOptions, ...options };
    // newOptions.headers = { ...newOptions.headers, token: tokenDodu };
    if (newOptions.method === 'POST' || newOptions.method === 'PUT') {
      if (!(newOptions.body instanceof FormData)) {
        newOptions.headers = {
          'Access-Control-Allow-Origin': '*',
          Accept: 'application/json',
          'Content-Type': 'application/json; charset=utf-8',
          ...newOptions.headers
        };
        newOptions.body = JSON.stringify(newOptions.body);
      } else {
        // newOptions.body is FormData
        newOptions.headers = {
          'Access-Control-Allow-Origin': '*',
          Accept: 'application/json',
          ...newOptions.headers,
        };
      }
    }

    return newOptions;
  }

  private checkStatus(response: any) {
    const statusMessage = {
      200: '服务器成功返回请求的数据。',
      201: '新建或修改数据成功。',
      202: '一个请求已经进入后台排队（异步任务）。',
      204: '删除数据成功。',
      400: '发出的请求有错误，服务器没有进行新建或修改数据的操作。',
      401: '用户没有权限（令牌、用户名、密码错误）。',
      403: '用户得到授权，但是访问是被禁止的。',
      404: '发出的请求针对的是不存在的记录，服务器没有进行操作。',
      406: '请求的格式不可得。',
      410: '请求的资源被永久删除，且不会再得到的。',
      422: '当创建一个对象时，发生一个验证错误。',
      500: '服务器发生错误，请检查服务器。',
      502: '网关错误。',
      503: '服务不可用，服务器暂时过载或维护。',
      504: '网关超时。',
    };

    if ((response.status >= 200 && response.status < 300) || response.status === 500) {
      return response;
    }
    const errortext = statusMessage[response.status] || response.statusText;
    // console.dir({
    //   message: `请求错误 ${response.status}: ${response.url}`,
    //   description: errortext,
    // });
    const error = new Error(errortext);
    error.name = response.status;
    // error.response = response;
    throw error;
  }
}
