import { HttpService } from './../../services/http.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { Vote } from 'src/app/models/vote';
import { Title } from '@angular/platform-browser';
import { Modal, Toast } from 'ng-zorro-antd-mobile';

@Component({
  selector: 'app-join',
  templateUrl: './join.component.html',
  styleUrls: ['./join.component.css'],
  providers: [Modal, Toast]
})
export class JoinComponent implements OnInit {
  private id: string;
  private vote: Vote;

  private files = [];

  constructor(
    private route: ActivatedRoute,
    private http: HttpService,
    private title: Title,
    private toast: Toast,
    private modal: Modal,
    private router: Router) {
    this.route.params.subscribe(params => {
      this.id = params.id;
    });
  }

  ngOnInit() {
    this.http
      .request(`vote/${this.id}`, { method: 'GET' })
      .then((vote: Vote) => {
        this.vote = vote;
        this.title.setTitle('参与 ' + vote.Name);
      });
  }

  onFilesChange(params) {
    const { files, type, index } = params;
    this.files = files;
  }

  onImageClick(params) {
    // console.log(params);
  }

  onJoinClick() {
    const images = new Array();

    if (this.files.length <= 0) {
      Toast.info('请至少选择一张图片', 3000, null, false);
      return;
    }

    this.files.forEach(e => {
      images.push(e.url);
    });

    const content = { VoteId: this.id, OpenId: '1+1', Images: images };

    this.http
      .request('contestantContent', { method: 'POST', body: content })
      .then(e => {
        Toast.info('报名成功', 5000, null, false);

        this.router.navigate([`v/${this.id}`]);
      });
  }
}
