import { Vote } from './../../models/vote';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { HttpService } from './../../services/http.service';

@Component({
  selector: 'app-vote-details',
  templateUrl: './vote-details.component.html',
  styleUrls: ['./vote-details.component.css']
})
export class VoteDetailsComponent implements OnInit {
  private id: string;
  private vote: Vote;
  private contestants: any[];
  private now: Date = new Date();

  private isCanJoin = false;

  constructor(
    private route: ActivatedRoute,
    private title: Title,
    private http: HttpService,
    private router: Router) {
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.id = params.id;
      this.loadVote();
      this.loadVoteContestant();
    });
  }

  onContestantClick = (id: string) => {
    this.router.navigateByUrl(`c/${id}`);
  }

  loadVoteContestant() {
    this.http.request(`vote/contestant/${this.id}`, { method: 'GET' })
      .then(contestants => {
        this.contestants = contestants;
      });
  }

  loadVote() {
    this.http
      .request(`vote/${this.id}`, { method: 'GET' })
      .then((vote: Vote) => {
        this.vote = vote;
        this.title.setTitle(vote.Name);
        // https://stackoverflow.com/questions/38843532/how-to-pass-multiple-parameter-to-directives-in-angular-with-typescript
        // https://angular.cn/guide/structural-directives
        // console.dir(vote);
        // console.dir(this.vote);
        const timeStarting = new Date(this.vote.VoteStartingTime).getTime();
        const timeClosing = new Date(this.vote.VoteClosingTime).getTime();
        const timeNow = new Date().getTime();

        if (timeNow < timeStarting) { this.isCanJoin = true; }
      });
  }

  onJoinClick = () => {
    this.router.navigate([`join/${this.vote.Id}`]);
  }
}
